.page {
  display: grid;
  gap: 14px;
}

.grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 14px;
}

.left {
  padding: 14px;
}

.tplList {
  display: grid;
  gap: 10px;
}

.tpl {
  border-radius: 14px;
  padding: 12px;
  background: var(--panel);
  border: 1px solid transparent;
  cursor: pointer;
}

.active {
  border-color: var(--accent);
}

.tplTitle {
  font-weight: 700;
}

.tplSub {
  font-size: 12px;
  color: var(--muted);
}

.controls {
  margin-top: 14px;
  display: grid;
  gap: 10px;
}

.right {
  display: grid;
  gap: 12px;
}

.results {
  display: grid;
  gap: 12px;
}

.resultCard {
  padding: 14px;
}

.loader {
  color: var(--accent);
}

.error {
  color: red;
}
